import uniStatusBar from "@/components/uni-ui/uni-status-bar/uni-status-bar.vue"

export default {
	components:{
		uniStatusBar
	},
	data() {
		return {
			username:"",
			password:"",
			check:true,
			
			//验证规则
			rules:{
				username:[
					{
						rule:/^[a-zA-Z]\w{5,17}$/,
						msg:"账号必须字母开头，长度在6~18之间，只能包含字母、数字和下划线"
					}
				],
				password:[
					{
						rule:/^.{5,20}$/,
						msg:"密码长度必须为5-20个字符"
					}
				]
			},
			
			focusClass:{
				username:false,
				password:false
			}
		}
	},
	methods: {
		goBack(){
			uni.navigateBack({
				delta: 1
			});
		},
		//表单验证
		validate(key) {
			var check = true
			this.rules[key].forEach((v)=>{
				//验证失败
				if (!(v.rule).test(this[key])) {
					uni.showToast({title:v.msg,icon:'none'})
					check = false
					return false
				}
			})
			return check
		},
		//提交表单
		submit(){
			if (!this.check) {
				return uni.showToast({
					title:'请先同意xxx协议'
				})
			}
			//验证用户名
			if (!this.validate('username')) return;
			//验证密码
			if (!this.validate('password')) return;
			console.log('提交成功');
			uni.request({
			    url: 'http://localhost:8080/shop/findAll', //仅为示例，并非真实接口地址。
				data:{
					
				},
				method:'GET',
			    header: {
			        'custom-header': 'hello' //自定义请求头信息
			    },
			    success: (res) => {
			        console.log(res.data);
			    }
			});
			uni.showLoading({
				title: '登录中...',
				mask: true
			});
			setTimeout(()=>{
				uni.hideLoading()
				uni.navigateBack({
					delta:1
				})
			},3000)
		},
		forget(){
			
		},
		focus(key){
			this.focusClass[key] = true
		},
		blur(key){
			this.focusClass[key] = false
		}
	}
}
